.Drawer {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 80%;
  max-width: 300px;
  padding: 20px 10px;
  box-sizing: border-box;
  background: #fff;
  z-index: 90;
  transform: translateX(0px);
  transition: transform 0.4s ease;
}

.Drawer.close {
  transform: translateX(-300px);
}

.Drawer ul {
  list-style: none;
  margin: 0;
  padding: 0;

}

.Drawer ul li {
  margin-bottom: 15px;
}

.Drawer ul li a {
  color: #363f54;
  font-size: 30px;
  text-decoration: none;
  background-color: #fff;
  padding: 0 20px 10px 20px;
  transition: opacity 0.4s ease;

}

.Drawer ul li a:hover, .active{
  opacity: 0.7;
}


.closeButton {
  position: absolute;
  color: #912929;
}